	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/targeting-multiple-browsers-with-css-and-some-javascript-trickery/index.md absPath of file >>> MarkdownRemark","html":"<p>There's a clever little javascript library that allows you to write CSS specifically for Chrome, the iPhone, IE 8, etc. all in a single file.  What it does is manipulate the HTML document client-side to add additional classes to the page, based on your browser type.  The end result is that your page gets additional selectors like .ie or .gecko added - and you can setup a single CSS file to target all of these browsers in one shot - no server side magic or conditional comments in the HTML.</p>\n<p>You can find more information here ( <a href=\"http://snipplr.com/view/9340/css-browser-selector/\">http://snipplr.com/view/9340/css-browser-selector/</a> ) or download it from here (<a href=\"http://github.com/rafaelp/css_browser_selector\">http://github.com/rafaelp/css_browser_selector</a> ).</p>","frontmatter":{"title":"Targeting multiple browsers with CSS... and some Javascript trickery.","date":"November 28, 2009","tags":["css","javascript"],"url":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/testing-performance-speed-and-best-practices-with-lighthouse/","tags":["performance","testing","progressive-web-apps"],"title":"Testing Performance, Speed and Best Practices with Lighthouse","image":null,"date":"2017-01-28T20:31:07.000Z"}},"next":{"frontmatter":{"path":"/blog/superscript-and-subscript-with-actionscript/","tags":["actionscript","external-links","flash","fonts","text-formatting"],"title":"Superscript and Subscript - with Actionscript*","image":null,"date":"2008-03-30T21:11:21.000Z"}}}}

